@mixin r($point) {
    @media (max-width: $point + px) { @content; }
}

.block{
    position: relative;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    margin: 15px 0 0;
    padding: 15px;
    display: flex;
    justify-content: flex-start;
    @include r(600){
       
    }
}

.section{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    @include r(600){
        flex-direction: column;
    }
}

.dialogs{
    margin-right: 15px;
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - 200px);
    height: 100%;
    overflow: scroll;

    @include r(600){
        overflow: auto;
        margin-right: 0;
        flex-direction: row;
        width: 100%;
    }
    &_item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;

        
        &:last-child{
            margin-bottom: 0;
        }
    }
}

.img{
    margin-bottom: 5px;
    height: 60px;
    width: 60px;
    border-radius: 100%;
}

